        <!-- <div class="coins-tabs-block" v-if="coins">
            <div class="coin-tab" :class="(coin.selected ? 'selected' : '')" v-for="(coin,idx) in coins" v-bind:key="idx" @click="selectCoin(idx)">
                <img class="coin-tab-coin-icon" :src="require(`@/assets/newImages/coins/icons/${$filters.lowercase(coin.coinTicker)}_icon.png`)">
