<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import IncreaseLimitModal from '../components/newModals/IncreaseLimitModal.vue'

export default defineComponent({
    data: () => ({
        selectedCoin: null,
        currTimestamp: +Date.now(),
        timerEndTimestamp: +Date.now(),
        showHighlight: false,
        coins: null,
        showIncreaseLimitModal: false
    }),
    setup() {
    },
    computed: {
        clientId() {
            return this.$store.getters.profile.id
        },
        auctionRunning() {
            return this.selectedCoin?.type === "live";
        },
        auctionFinished() {
            return this.selectedCoin?.type === "finished";
        },
        currentCoinName() {
            return this.selectedCoin?.coin;
        },
        youCanBuy(){
            return this.selectedCoin ? Math.min((this.selectedCoin.youCanBuy || 0) + this.selectedCoin.limit, (this.selectedCoin.maxUSDBuyAmount || Infinity) / this.startPrice) : 0;
        },

        timeLeft() {
            let totalTime = Math.floor((this.timerEndTimestamp - this.currTimestamp) / 1000);
            if (totalTime <= 0) {
                if (this.selectedCoin?.type == "announce") {
                    this.startAuction();
                }
                return "00:00";
            }
            let hours = Math.floor(totalTime / 3600);
            let otherTime = totalTime % 3600;
            let minutes = Math.floor(otherTime / 60);
            let seconds = Math.floor(otherTime % 60);
            return `${hours}:${minutes >= 10 ? minutes : ("0" + minutes)}:${seconds >= 10 ? seconds : ("0" + seconds)}`;
        },
        startPriceText() {
            // if (this.selectedCoin?.yourStartPrice == "startPriceDOMHolder") {
                return localizeFilter("starting_price_for_DOM_coin_holders");
            // }
            // else if (this.selectedCoin?.yourStartPrice == "startPriceDOMHolderReferral") {
            //     return localizeFilter("starting_price_for_DOM_coin_holders_referral");
            // }
            // else {
            //     return localizeFilter("starting_price");
            // }
        },
        startPrice() {
            // if (this.selectedCoin?.yourStartPrice == "startPriceDOMHolder") {
                return this.selectedCoin?.startPriceDOMHolder;
            // }
            // else if (this.selectedCoin?.yourStartPrice == "startPriceDOMHolderReferral") {
            //     return this.selectedCoin?.startPriceDOMHolderReferral;
            // }
            // else {
            //     return this.selectedCoin?.startPriceOther;
            // }
        }
    },
    methods: {
        increaseLimit() {
            this.showIncreaseLimitModal = true
        },
        closeModals(needUpdateInfo) {
            this.showIncreaseLimitModal = false
            if(needUpdateInfo) {
                this.loadData();
            }
        },
        selectCoin(idx) {
            for (let i = 0; i < this.coins.length; i++) {
                if (i === idx) {
                    this.coins[i].selected = true;
                    this.selectedCoin = this.coins[i];
                    this.setupTimers();
                }
                else {
                    this.coins[i].selected = false;
                }
            }
        },
        setupTimers() {
            clearInterval(this.timerInterval);
            this.timerEndTimestamp = this.auctionRunning || this.auctionFinished ? this.selectedCoin?.presaleEndTimestamp : this.selectedCoin?.presaleStartTimestamp;
            this.timerInterval = setInterval(() => {
                this.currTimestamp = Date.now();
            }, 1000);
        },
        startAuction() {
            if (!this.auctionRunning) {
                this.auctionRunning = true;
                clearInterval(this.timerInterval);
                this.loadData();
            }
        },
        async buyCoin(coinTicker, maxAmount) {
            if (!coinTicker) {return}
            await this.$store.dispatch('setPaymentCoinTicker', {coinTicker: coinTicker})
            await this.$store.dispatch('setPaymentCoinMaxAmount', {maxAmount: maxAmount})
            this.$nextTick(()=>{
                this.$store.dispatch('setShowPaymentCoinModule', {value: true})
            })
        },
        openCoinPage() {
            //window.open('#/coins/' + this.currentCoinName)
        },
        async loadData() {
            try {
                const serverData = (await this.$store.dispatch("getClosedPresaleParameters")).coins;
                this.coins = serverData;
                if (serverData.length > 0) {
                    this.selectCoin(0);
                }
                // // console.log(this.coins, this.selectedCoin)
            }
            catch (e) {
                this.$error(e);
            }
        }
    },
    created: function () {
        this.timerInterval = null;
    },
    async mounted() {
        await this.loadData();
        const exchangeClosedAuctionPageOpened = JSON.parse(localStorage.exchangeClosedAuctionPageOpened ?? null);
        this.showHighlight = true; //(exchangeClosedAuctionPageOpened != true)
        if (exchangeClosedAuctionPageOpened != true) {
            localStorage.exchangeClosedAuctionPageOpened = true;
            window.dispatchEvent(new CustomEvent("exchangeClosedAuctionPageOpened", {
                detail: {
                    value: localStorage.getItem("exchangeClosedAuctionPageOpened")
                }
            }));
        }
        setTimeout(() => {
            this.showHighlight = false;
        }, 2000);
    },
    unmounted() {
        clearInterval(this.timerInterval);
    },
    components: { IncreaseLimitModal }
})
</script>

